.icon {
  color: #666;
}

.name {
  margin-left: 0.5em;
}

.sub-name {
  font-size: 0.85em;
  color: #56575e;
}

.node {
  padding: 0.5em 0;
  cursor: pointer;
  color: #444;
}

.node.disabled {
  cursor: auto;
  color: #56575e;
}

.caret {
  padding-right: 0.33em;
  color: #666;
  width: 1em;
}

.container {
  width: 100%;
}

