.title {
  color: #444;
  font-size: 20px;
  margin: 12px 15px;
}

.legacyLink {
  line-height: 2;
  text-align: right;
  font-size: 12px;
  float: right;
}

.header-container {
  margin: 0 15px;
}

.header {
  font-size: 24px;
  padding: 0.75em 0;
  color: #444;
}

.align-middle {
  display: inline-block;
  vertical-align: middle;
}

.time-range {
  height: 2.92807rem;
  margin: 0 0 1.333rem;
  padding: 0.6665rem;
  appearance: none;
  border: 1px solid #666;
  border-radius: 0;
  background-color: #fff;
  font-family: inherit;
  font-size: 1rem;
  line-height: normal;
  color: #000;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' width='32' height='24' viewBox='0 0 32 24'><polygon points='0,0 32,0 16,24' style='fill: rgb%2851, 51, 51%29'></polygon></svg>");
  background-origin: content-box;
  background-position: right -1.333rem center;
  background-repeat: no-repeat;
  background-size: 9px 6px;
  padding-right: 1.9995rem;
  transition: box-shadow 0.5s, border-color 0.25s ease-in-out;
  min-width: 10em;
  margin: 0;
  border: 1px solid #e2e6ea;
  height: 36px;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' width='32' height='24' viewBox='0 0 32 24'><polygon points='0,0 32,0 16,24' style='fill: rgb%28136, 136, 136%29'></polygon></svg>");
}

@media screen and (min-width: 0\0) {
  .time-range {
    background-image: url("data:image/png;base64,");
  }
}

.time-range:focus {
  outline: none;
  border: 1px solid #36373b;
  background-color: #fff;
  box-shadow: 0 0 5px #666;
  transition: box-shadow 0.5s, border-color 0.25s ease-in-out;
}

.time-range:disabled {
  background-color: #e2e6ea;
  cursor: default;
}

.time-range::-ms-expand {
  display: none;
}

.time-range[multiple] {
  height: auto;
  background-image: none;
}

@media screen and (min-width: 0\0) {
  .time-range {
    background-image: url("data:image/png;base64,");
  }
}

.pool-selector {
  position: relative;
  display: inline-block;
  height: 36px;
}

.menu {
  max-height: 60vh;
  overflow-y: scroll;
  background-color: white;
}

.label {
  font-size: 14px;
  color: #888;
  margin-right: 10px;
  margin-bottom: 0;
  font-weight: normal;
}

.container {
  background-color: #fff;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container:before {
  display: table;
  content: " ";
  flex-basis: 0;
  order: 1;
}

