@import "vendor/foundation/import-variables";
@import "../shared";

.popover-link {
    border-bottom: 1px dotted $dark-grey-02;
    cursor: pointer;
    display: inline;
}

.detail {
    border-bottom: 1px solid $medium-grey-03;
    box-sizing: border-box;
    width: 16em;

    &:last-child {
        border-bottom: none;
    }
}

.bar {
    margin: 0 10px;
    font-size: 24px;
}

.popover {
    padding: 0.5em;
    box-shadow: 2px 2px 5px 0 $label-color;
    margin-top: 0.5em;
    font-weight: $global-weight-normal;
    border: 1px solid $light-grey-01;
    background-color: $white;
}

.savings-name {
    font-size: 11px;
}

.savings-value {
    font-size: 16px;
}
