@import "vendor/foundation/import-variables";
@import "../layout/style";

.legend {
    background: white;
    position: relative;
    overflow-x: hidden;
    width: 170px;
    color: $medium-grey-02;
    font-weight: $global-weight-normal;
    font-family: $body-font-family;
    font-size: 11px;
}

.legend:before {
    float: left;
    width: 0;
    white-space: nowrap;
    content: ". . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . ";
}

.left {
    background: inherit;
    padding-right: 0.2em;float: left;
}

.right {
    float: right;
    background: inherit;
    padding-left: 0.2em;
    color: $dark-grey-02;
}

.striped {
    $fill-color: #B0CED4;
    $background-color: $light-grey-04;
    $gradient-params: -45deg, transparent, transparent 1px, $fill-color 2px, $background-color 3px;

    background-color: $background-color;
    background: -moz-repeating-linear-gradient(#{$gradient-params});
    background: -webkit-repeating-linear-gradient(#{$gradient-params});
    background: -o-repeating-linear-gradient(#{$gradient-params});
    background: -ms-repeating-linear-gradient(#{$gradient-params});
    background: repeating-linear-gradient(#{$gradient-params});
}

.bar-chart {
    width: 100%;
    margin-bottom: 1rem;
    position: relative;
    background-color: $light-grey-02;
    height: 1rem;
}
