@import "vendor/foundation/import-variables";
.center-align-column {
    text-align: center;
}

.right-align-column {
    text-align: right;
}

.large-column {
    width: 20em;
}

.medium-column {
    width: 12em;
}

.small-column {
    width: 8em;
}

.table {

    // Summary
    [data-colname="name"] {
        @extend .large-column;
    }

    [data-colname="appCategory"] {
        @extend .center-align-column;
        @extend .medium-column;
    }

    [data-colname="hostName"],
    [data-colname="poolName"],
    [data-colname="groupName"] {
        @extend .medium-column;
    }
}
