@import "vendor/foundation/import-variables";
@import "util/unit";

.legend {
    width: 15rem;
    font-weight: 200;
    margin: 5px 0;
}

%legend-section {
    background: inherit;
    padding-right: 0.2em;
    width: 60%;
    display: inline-block;
}

.legend-name {
    @extend %legend-section;
    width: 60%;
}

.legend-value {
    @extend %legend-section;
    text-align: right;
    width: 25%;
}

.legend-unit {
    @extend %legend-section;
    width: 15%;
}

.striped {
    $fill-color: #B0CED4;
    $background-color: $light-grey-04;
    $gradient-params: -45deg, transparent, transparent 1px, $fill-color 2px, $background-color 3px;

    background-color: $background-color;
    background: -moz-repeating-linear-gradient(#{$gradient-params});
    background: -webkit-repeating-linear-gradient(#{$gradient-params});
    background: -o-repeating-linear-gradient(#{$gradient-params});
    background: -ms-repeating-linear-gradient(#{$gradient-params});
    background: repeating-linear-gradient(#{$gradient-params});
}

.bar-chart {
    width: 100%;
    margin-bottom: 1rem;
    position: relative;
    background-color: $light-grey-02;
    height: 1rem;
}

.bar {
    height: inherit;
    display: inline-block;
    border-right: 1px solid $white;
    box-sizing: border-box;
}

.caret {
    position: absolute;
    color: #f1c232;
    font-size: 1.5rem;
    bottom: -1.3rem;
}

.swatch {
    width: 10px;
    height: 10px;
    border-radius: 0;
    border: none;
    display: inline-block;
    margin: 0.1rem 0.2rem -1px 0;
}

.is-zero {
    display: none;
}

