@import "vendor/foundation/import-variables";
.partitionBorder {
    border: solid 0px #E2E6EA;
}

.leftPartitionBorder {
    @extend .partitionBorder;
    border-left-width: 1px;
    padding-left: 26px;
}

.controllerBox {
    font-weight: bold;
    font-size: 12px;
    line-height: 24px;
    height: 24px;
    width: 30px;
    color: $white;
    background: $dark-grey-02;
}

.badController {
    background: #f04953; // text-danger color
}

.capitalize {
    text-transform: capitalize;
}

.addTopPadding {
    padding-top: 4px;
}