@import "vendor/foundation/import-variables";
.trend {
    color: $dark-grey-02;
}

.popover-link {
    border-bottom: 1px dashed $medium-grey-02;
    cursor: pointer;
    margin-bottom: 0.5em;
}

.tooltip {
    max-width: 30em;
    padding: 0.5em;
    box-shadow: 2px 2px 5px 0 $medium-grey-03;
    margin-top: 0.5em;
    border: 1px solid $medium-grey-03;
    background-color: $white;
    font-size: 0.86rem;
    color: $dark-grey-02;
}
