.trend {
  color: #444;
}

.popover-link {
  border-bottom: 1px dashed #888;
  cursor: pointer;
  margin-bottom: 0.5em;
}

.tooltip {
  max-width: 30em;
  padding: 0.5em;
  box-shadow: 2px 2px 5px 0 #ccc;
  margin-top: 0.5em;
  border: 1px solid #ccc;
  background-color: #fff;
  font-size: 0.86rem;
  color: #444;
}

