@import "vendor/foundation/import-variables";
@import "forms/select";

.title {
    color: $dark-grey-02;
    font-size: 20px;
    margin: 12px 15px;
}

.legacyLink {
    line-height: 2;
    text-align: right;
    font-size: 12px;
    float: right;
}

.header-container {
    margin: 0 15px;
}

.header {
    font-size: 24px;
    padding: 0.75em 0;
    color: $dark-grey-02;
}

.align-middle {
    display: inline-block;
    vertical-align: middle;
}

.time-range {
    @include form-select;
    min-width: 10em;
    margin: 0;
    border: 1px solid $light-grey-01;
    height: 36px;
    @include background-triangle($medium-grey-02);
}

.pool-selector {
    position: relative;
    display: inline-block;
    height: 36px;
}

.menu {
    max-height: 60vh;
    overflow-y: scroll;
    background-color: white;
}

.label {
    font-size: 14px;
    color: $medium-grey-02;
    margin-right: 10px;
    margin-bottom: 0;
    font-weight: normal;
}

.container {
    background-color: $white;
    margin-right: auto;
    margin-left: auto;
    padding-left: 15px;
    padding-right: 15px;

    &:before {
        display: table;
        content: " ";
        flex-basis: 0;
        order: 1;
    }
}
