@import "vendor/foundation/import-variables";
:global .nmbl-dropdown {
    position: relative;
    color: $dark-grey-02;
}

.menu {
    position: absolute;
    z-index: 100;
    min-width: 100%;
    border: 1px solid $light-grey-01;
    padding: 0;
    margin: 0;
    box-shadow: 2px 2px 1px $light-grey-02;
    background-color: $white;
}

.menu-item-container {
    list-style: none;
    width: 100%;
    cursor: pointer;
    padding: 0;

    :hover {
        background-color: $light-grey-02;
    }
}

.menu-item {
    background-color: transparent;
    border: 0;
    outline: none;
    width: 100%;
    text-align: left;
    padding: 4px 10px;
}

.toggle {
    outline: none;
    text-align: left;
    padding: 4px 10px;
    background-color: white;
    border: 1px solid $light-grey-01;
    width: 100%;
    min-width: 15em;
    border-radius: 0;
    height: inherit;

    &.active {
        border-bottom-color: transparent;
    }

    &:disabled {
        background-color: $input-background-disabled;
        cursor: default;
    }
}

.caret {
    font-size: 14px;
    color: $medium-grey-02;
    margin-left: 0.5em;
}

.align-middle {
    display: inline-block;
    vertical-align: middle;
}
