@import "vendor/foundation/import-variables";
@mixin nmbl-Progress {
    $height: 15px;
    $thin-height: 5px;
    $translate-y: 10px;

    .nmbl-ProgressWithValue {
        position: relative;

        %value {
            //font-size: 1rem;
            position: absolute;
            top: -$translate-y;
        }

        &__left {
            @extend %value;

            left: 0;
        }

        &__right {
            @extend %value;

            color: $medium-grey-02;
            right: 0;
        }

        .progress {
            margin: 0;
            border-radius: 0;
            background-color: $light-grey-01;
            box-shadow: none;
        }

        .progress.thin {
            top: $translate-y;
        }
    }

    .progress.thin {
        line-height: $height;
        height: $thin-height;
        position: relative;
    }
}

:global {
    @include nmbl-Progress;
}
