@import "vendor/foundation/import-variables";
.icon {
    color: $medium-grey-01;
}

.name {
    margin-left: 0.5em;
}

.sub-name {
    font-size: 0.85em;
    color: $dark-grey-03;
}

.node {
    padding: 0.5em 0;
    cursor: pointer;
    color: $dark-grey-02;

    &.disabled {
        cursor: auto;
        color: $dark-grey-03;
    }
}

.caret {
    padding-right: 0.33em;
    color: $medium-grey-01;
    width: 1em;
}

.container {
    width: 100%;
}
