.metric {
  margin: 0 0 10px;
}

.value {
  font-size: 28px;
  font-weight: 200;
  color: #444;
}

.unit {
  font-size: 14px;
  margin-left: 5px;
}

.subtitle {
  text-transform: uppercase;
}

.muted {
  opacity: 0.7;
}

.layout > section {
  padding-bottom: 30px;
}

.layout > section strong {
  color: #36373b;
}

.layout > section:not(.inset) {
  margin: 0 15px;
}

.layout h1 {
  color: #444;
  font-size: 20px;
  line-height: 1.5em;
  margin: 0;
  font-weight: normal;
}

.layout h2 {
  font-size: 1em;
  text-transform: none;
  color: #666;
  margin: 0;
  font-weight: normal;
}

.layout h3 {
  font-size: 1em;
  text-transform: uppercase;
  color: #666;
}

.layout :global .ReactTabs__Tab--disabled {
  display: none;
}

.summary > div {
  display: inline-block;
  vertical-align: top;
}

.summary > div:not(:last-child) {
  margin-right: 60px;
}

.inset {
  border: 10px solid #e2e6ea;
}

.inset:not(:last-child) {
  border-bottom-style: none;
}

.time-series {
  padding: 15px;
}

.time-series-chart {
  height: 340px;
}

.time-series-chart > div > div > div {
  overflow: visible !important;
}

.top-pools :global .elmer-ChartUtils-SharedTooltips {
  text-align: center;
  white-space: normal;
  margin-top: -10px;
}

.chart {
  padding-left: 20px;
  width: 350px;
  height: 100px;
}

.bar-chart {
  width: 320px;
}

.total-usage {
  height: inherit;
}

.folder-limit {
  margin-top: 10px;
}

.system-config dl {
  display: inline-block;
  vertical-align: top;
  color: #444;
}

.system-config dl > dt:not(:first-of-type) {
  margin-top: 0.5em;
}

