@import "vendor/foundation/import-variables";
h3.chartLabel {
    font-size: 1rem;
    margin-bottom: 0;
}

.gridRowChartContainer {
    margin-top: 2rem;
    margin-bottom: 2rem;

    &:first-of-type {
        margin-top: 0;
    }
}

.progressContainer {
    margin-top: .5rem;
    margin-bottom: 2rem;
}

.summaryCharts {
    border: 1px solid #CCCCCC;
    padding: 1em;
    /* height of tooltip above charts;
    we want to line up with the actual chart content rather than the tooltip and title */
    margin-top: 19px;
}

.paddedLegend {
    padding: 1em;
}
