:global .nmbl-ProgressWithValue {
  position: relative;
}

:global .nmbl-ProgressWithValue .nmbl-ProgressWithValue__left,
:global .nmbl-ProgressWithValue .nmbl-ProgressWithValue__right {
  position: absolute;
  top: -10px;
}

:global .nmbl-ProgressWithValue__left {
  left: 0;
}

:global .nmbl-ProgressWithValue__right {
  color: #888;
  right: 0;
}

:global .nmbl-ProgressWithValue .progress {
  margin: 0;
  border-radius: 0;
  background-color: #e2e6ea;
  box-shadow: none;
}

:global .nmbl-ProgressWithValue .progress.thin {
  top: 10px;
}

:global .progress.thin {
  line-height: 15px;
  height: 5px;
  position: relative;
}

