.donut {
  position: relative;
}

.donut-pie {
  padding: 0;
  height: 100px;
  width: 100px;
}

.donut-title {
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 2px;
  width: 100%;
  height: 100%;
}

.percentUsed {
  color: #888;
  font-size: 2em;
}

.percentUsedWarning {
  color: #f04953;
  font-size: 2em;
}

.percentageGrowth {
  position: relative;
  top: -8px;
  left: -4px;
}

.strongText {
  font-weight: bold;
}

.striped {
  background-color: #f4f6f9;
  background: -moz-repeating-linear-gradient(-45deg, #fff, #fff 1px, #B0CED4 2px, #f4f6f9 3px);
  background: -webkit-repeating-linear-gradient(-45deg, #fff, #fff 1px, #B0CED4 2px, #f4f6f9 3px);
  background: -o-repeating-linear-gradient(-45deg, #fff, #fff 1px, #B0CED4 2px, #f4f6f9 3px);
  background: -ms-repeating-linear-gradient(-45deg, #fff, #fff 1px, #B0CED4 2px, #f4f6f9 3px);
  background: repeating-linear-gradient(-45deg, #fff, #fff 1px, #B0CED4 2px, #f4f6f9 3px);
}

