:global .nmbl-dropdown {
  position: relative;
  color: #444;
}

.menu {
  position: absolute;
  z-index: 100;
  min-width: 100%;
  border: 1px solid #e2e6ea;
  padding: 0;
  margin: 0;
  box-shadow: 2px 2px 1px #eef0f2;
  background-color: #fff;
}

.menu-item-container {
  list-style: none;
  width: 100%;
  cursor: pointer;
  padding: 0;
}

.menu-item-container :hover {
  background-color: #eef0f2;
}

.menu-item {
  background-color: transparent;
  border: 0;
  outline: none;
  width: 100%;
  text-align: left;
  padding: 4px 10px;
}

.toggle {
  outline: none;
  text-align: left;
  padding: 4px 10px;
  background-color: white;
  border: 1px solid #e2e6ea;
  width: 100%;
  min-width: 15em;
  border-radius: 0;
  height: inherit;
}

.toggle.active {
  border-bottom-color: transparent;
}

.toggle:disabled {
  background-color: #e2e6ea;
  cursor: default;
}

.caret {
  font-size: 14px;
  color: #888;
  margin-left: 0.5em;
}

.align-middle {
  display: inline-block;
  vertical-align: middle;
}

