@import "../shared";

.layout {

    > section {
        padding-bottom: 30px;

        strong {
            color: $dark-grey-01;
        }

        &:not(.inset) {
            margin: 0 15px;
        }
    }

    h1 {
        color: $dark-grey-02;
        font-size: 20px;
        line-height: 1.5em;

        // Override global styles from legacy app
        margin: 0;
        font-weight: normal;
    }

    h2 {
        font-size: 1em;
        text-transform: none;
        color: $medium-grey-01;

        // Override global styles from legacy app
        margin: 0;
        font-weight: normal;
    }

    h3 {
        font-size: 1em;
        text-transform: uppercase;
        color: $medium-grey-01;
    }

    // TODO move this when elmer is merged in
    :global {
        .ReactTabs__Tab--disabled {
            display: none;
        }
    }
}

.summary {
    //display: inherit;

    > div {
        display: inline-block;
        vertical-align: top;

        &:not(:last-child) {
            margin-right: 60px;
        }
    }
}

.inset {
    border: 10px solid $light-grey-01;

    &:not(:last-child) {
        border-bottom-style: none;
    }
}

.time-series {
    padding: 15px;
}

.time-series-chart {
    height: 340px;

    > div > div > div {
        overflow: visible !important;
    }
}

.top-pools {
    :global {
        .elmer-ChartUtils-SharedTooltips {
            text-align: center;
            white-space: normal;
            margin-top: -10px;
        }
    }
}

.chart {
    padding-left: 20px;
    width: 350px;
    height: 100px;
}

.bar-chart {
    width: 320px;
}

.total-usage {
    height: inherit;
}

.folder-limit {
    margin-top: 10px;
}

.system-config {
    dl {
        display: inline-block;
        vertical-align: top;
        color: $dark-grey-02;

        > dt:not(:first-of-type) {
            margin-top: 0.5em;
        }
    }
}
