.metric {
  margin: 0 0 10px;
}

.value {
  font-size: 28px;
  font-weight: 200;
  color: #444;
}

.unit {
  font-size: 14px;
  margin-left: 5px;
}

.subtitle {
  text-transform: uppercase;
}

.muted {
  opacity: 0.7;
}

.popover-link {
  border-bottom: 1px dotted #444;
  cursor: pointer;
  display: inline;
}

.detail {
  border-bottom: 1px solid #ccc;
  box-sizing: border-box;
  width: 16em;
}

.detail:last-child {
  border-bottom: none;
}

.bar {
  margin: 0 10px;
  font-size: 24px;
}

.popover {
  padding: 0.5em;
  box-shadow: 2px 2px 5px 0 #fff;
  margin-top: 0.5em;
  font-weight: normal;
  border: 1px solid #e2e6ea;
  background-color: #fff;
}

.savings-name {
  font-size: 11px;
}

.savings-value {
  font-size: 16px;
}

