@import "vendor/foundation/import-variables";
.donut {
    position: relative;
}

.donut-pie {
    padding: 0;
    height: 100px; // TODO: Standardize Chart dimensions
    width: 100px;
}

.donut-title {
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 2px;
    width: 100%;
    height: 100%;
}

.percentUsed {
    color: $medium-grey-02;
    font-size: 2em;
}

.percentUsedWarning {
    color: $red-01;
    font-size: 2em;
}

.percentageGrowth {
    position: relative;
    top: -8px;
    left: -4px;
}

.strongText {
    font-weight: bold;
}

.striped {
    $fill-color: #B0CED4;
    $background-color: $light-grey-04;
    $gradient-params: -45deg, $white, $white 1px, $fill-color 2px, $background-color 3px;
    background-color: $background-color;
    background: -moz-repeating-linear-gradient(#{$gradient-params});
    background: -webkit-repeating-linear-gradient(#{$gradient-params});
    background: -o-repeating-linear-gradient(#{$gradient-params});
    background: -ms-repeating-linear-gradient(#{$gradient-params});
    background: repeating-linear-gradient(#{$gradient-params});
}
